import React, { Component } from 'react';
import styles from './Home.module.css';

// Navbar
import Navbar from '../../components/Navbar/Navbar';
import NavItem from '../../components/Navbar/NavItem/NavItem';

import Row from '../../components/row/Row';

// Card
import Card from '../../components/Card/Card';

// Logo
import virus from '../../assets/pictures/virus.svg';
import strength from '../../assets/pictures/strength.svg';
import fever from '../../assets/pictures/fever.svg';
import death from '../../assets/pictures/death.svg';

//
import NumberFormat from 'react-number-format';

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      confirmed: 0,
      recovered: 0,
      hospitalized: 0,
      deaths: 0,
      newConfirmed: 0,
      newRecoverd: 0,
      newHospitalized: 0,
      newDeath: 0,
      updateDate: '',
    };
  }

  componentDidMount() {
    fetch('https://covid19.th-stat.com/api/open/today')
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
          this.setState({
            confirmed: result.Confirmed,
            recovered: result.Recovered,
            hospitalized: result.Hospitalized,
            deaths: result.Deaths,
            newConfirmed: result.NewConfirmed,
            newRecoverd: result.NewRecoverd,
            newHospitalized: result.NewHospitalized,
            newDeath: result.NewDeaths,
            updateDate: result.UpdateDate,
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  }

  render() {
    return (
      <div>
        <Navbar>
          <NavItem>ภาพรวม</NavItem>
          <NavItem disable={true}>สถิติ</NavItem>
          <NavItem disable={true}>ไทม์ไลน์</NavItem>
          <NavItem disable={true}>ภูมิภาค</NavItem>
          <NavItem url='https://ko-fi.com/isamuch' newTab={true}>
            สนับสนุนค่ากาแฟ
          </NavItem>
        </Navbar>
        <div className={styles.contain}>
          <section>
            <Row>
              <Card>
                <h1>
                  ยอดผู้ติดเชื้อสะสมใน <b>"ประเทศไทย"</b>
                </h1>
                <div className={styles.col4}>
                  <div className={styles.showData}>
                    <img
                      src={virus}
                      alt='virus-logo'
                      className={styles.logoPic}
                    />
                    <h3>ติดเชื้อสะสม</h3>
                    <h2>
                      <NumberFormat
                        value={this.state.confirmed}
                        displayType={'text'}
                        thousandSeparator={true}
                      />
                    </h2>
                    {this.state.newConfirmed > 0 ? (
                      <p>
                        [ เพิ่มขึ้น{' '}
                        <NumberFormat
                          value={this.state.newConfirmed}
                          displayType={'text'}
                          thousandSeparator={true}
                        />{' '}
                        ]
                      </p>
                    ) : null}
                  </div>
                  <div className={styles.showData}>
                    <img
                      src={strength}
                      alt='strength-logo'
                      className={styles.logoPic}
                    />
                    <h3>หายแล้ว</h3>
                    <h2>
                      <NumberFormat
                        value={this.state.recovered}
                        displayType={'text'}
                        thousandSeparator={true}
                      />
                    </h2>
                  </div>
                  <div className={styles.showData}>
                    <img
                      src={fever}
                      alt='fever-logo'
                      className={styles.logoPic}
                    />
                    <h3>กำลังรักษา</h3>
                    <h2>
                      <NumberFormat
                        value={this.state.hospitalized}
                        displayType={'text'}
                        thousandSeparator={true}
                      />
                    </h2>
                  </div>
                  <div className={styles.showData}>
                    <img
                      src={death}
                      alt='death-logo'
                      className={styles.logoPic}
                    />
                    <h3>เสียชีวิต</h3>
                    <h2>
                      <NumberFormat
                        value={this.state.deaths}
                        displayType={'text'}
                        thousandSeparator={true}
                      />
                    </h2>
                    {this.state.newDeath > 0 ? (
                      <p>
                        [ เพิ่มขึ้น{' '}
                        <NumberFormat
                          value={this.state.newDeath}
                          displayType={'text'}
                          thousandSeparator={true}
                        />{' '}
                        ]
                      </p>
                    ) : null}
                  </div>
                </div>
                <div>
                  <p>อัพเดทข้อมูลล่าสุด : {this.state.updateDate}</p>
                </div>
              </Card>
            </Row>
          </section>
        </div>
        <footer className={styles.footer}>
          <Row>
            <div className={styles.footerLayout}>
              <span>Copyright&nbsp;©&nbsp;2020&nbsp;iSamuch</span>
              <span className={styles.split}>&nbsp;|&nbsp;</span>
              <span>Powered&nbsp;by&nbsp;iSamuch</span>
              <span className={styles.split}>&nbsp;|&nbsp;</span>
              <span>
                Icons&nbsp;made&nbsp;by&nbsp;
                <a
                  href='https://www.flaticon.com/authors/ultimatearm'
                  title='ultimatearm'
                >
                  ultimatearm
                </a>
                &nbsp;and&nbsp;
                <a
                  href='https://www.flaticon.com/authors/freepik'
                  title='Freepik'
                >
                  Freepik
                </a>
                &nbsp; from&nbsp;
                <a href='https://www.flaticon.com/' title='Flaticon'>
                  www.flaticon.com
                </a>
              </span>
              <span className={styles.split}>&nbsp;|&nbsp;</span>
              <span>
                API from{' '}
                <a href='https://www.moph.go.th/' title='moph'>
                  กระทรวงสาธารณสุข
                </a>
              </span>
            </div>
          </Row>
        </footer>
      </div>
    );
  }
}
